body {
  overflow: hidden;
  background-color: #282c34;
  text-align: center;
  color: white; }

.header {
  padding-top: 25px;
  font-size: calc(40px + 2vmin);
  font-weight: 600; }

.header-selection {
  padding-left: 5px;
  padding-right: 4px;
  margin-left: 5px;
  border-radius: 3px;
  color: black;
  background-color: orange; }

#loaderPlaceholder, #loader {
  width: 100%;
  height: 1px;
  position: absolute;
  top: 115px;
  left: 0; }

#loaderPlaceholder {
  background: #737373; }

#loader {
  background: orange;
  z-index: 1;
  transform: scaleX(0);
  transition: 0.75s; }

.signs {
  position: absolute;
  bottom: 0;
  height: 25px;
  width: 100%;
  text-align: center;
  padding-bottom: 10px; }

@keyframes loading {
  0% {
    transform: scaleX(0); }
  50% {
    transform: scaleX(1); } }
